/*管理画面のSCSS*/

.wp-block-create-block-pfmodal input[type="text"] {
	font-family: sans-serif;
	font-size: 64px;
	color: inherit;
	background: inherit;
	border: 0;
}


.wp-block-create-block-pfmodal{
	width: 100%;
    border-radius: 2px;
    box-shadow: inset 0 0 0 1px #1e1e1e;
    box-sizing: border-box;
    color: #1e1e1e;
    margin: 0;
    outline: 1px solid transparent;
    padding: 1em;
    position: relative;
    text-align: left;
}